import {Box, Button, RadioGroup, Typography, Radio, FormControlLabel, Grid, TextField} from '@mui/material'
import React, {FC, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux';
import {DeliverySelector} from "../../../store/selectors/selectors";
import {AppDispatch} from "../../../store/store";
import CustomAlert from "../../../components/alert";
import {decrementStep, setDateData} from "../../../store/slices/delivery-slice";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import {DatePicker} from "@mui/x-date-pickers/DatePicker";
import {LocalizationProvider} from "@mui/x-date-pickers/LocalizationProvider";
import dayjs, {Dayjs} from "dayjs";
import {TimePicker} from "@mui/x-date-pickers";

const DateComponent: FC = () => {
    const [open, setOpen] = useState(false)
    const {
        ready: readyStore,
        date: dateStore,
        appNumber,
        readyDate: readyDateStore,
        readyTime: readyTimeStore
    } = useSelector(DeliverySelector)
    const dispatch = useDispatch<AppDispatch>()

    const [date, setDate] = useState<Dayjs | null>(dateStore)
    const [ready, setReady] = useState(readyStore)

    const [readyDate, setReadyDate] = useState<Dayjs | null>(readyDateStore)
    const [readyTime, setReadyTime] = useState<Dayjs | null>(readyTimeStore)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const ready = data.get('ready') as string
        const appNumber = data.get('appNumber') as string

        if (ready && appNumber && date &&
            ((ready === 'заказ не готов' && readyDate && readyTime) || ready === 'заказ готов')
        ) {
            dispatch(setDateData({ready, appNumber, date, readyDate, readyTime}))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Укажите следующие данные:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            name="appNumber"
                            label="Номер заказа"
                            fullWidth
                            autoComplete="order-number"
                            defaultValue={appNumber}
                        />
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <LocalizationProvider
                            dateAdapter={AdapterDayjs}
                            adapterLocale='ru'
                        >
                            <DatePicker
                                minDate={dayjs().add(1, 'day')}
                                label="Дата забора"
                                value={date}
                                onChange={e => setDate(e)}
                                renderInput={(params) => <TextField fullWidth {...params} />}
                            />
                        </LocalizationProvider>
                    </Grid>
                </Grid>
                <RadioGroup
                    aria-labelledby="pick-up-from"
                    name="radio-buttons-group"
                    value={ready}
                    onChange={e => setReady(e.target.value)}
                >
                    <FormControlLabel name='ready' value="заказ готов" control={<Radio/>} label="Заказ готов"/>
                    <FormControlLabel name='ready' value="заказ не готов" control={<Radio/>} label="Заказ не готов"/>
                </RadioGroup>
                {
                    ready === "заказ не готов" &&
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                            <LocalizationProvider
                                dateAdapter={AdapterDayjs}
                                adapterLocale='ru'
                            >
                                <DatePicker
                                    minDate={dayjs()}
                                    label="Дата готовности"
                                    value={readyDate}
                                    onChange={e => setReadyDate(e)}
                                    renderInput={(params) => <TextField fullWidth {...params} />}
                                />
                            </LocalizationProvider>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <LocalizationProvider
                                dateAdapter={AdapterDayjs}
                                adapterLocale='ru'
                            >
                                <TimePicker
                                    label="Время готовности"
                                    value={readyTime}
                                    onChange={(newValue) => setReadyTime(newValue)}
                                    renderInput={(params) => <TextField fullWidth {...params} />}
                                />
                            </LocalizationProvider>
                        </Grid>
                    </Grid>
                }
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button type="submit" variant="contained">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default DateComponent
