import {
    Box,
    Button,
    Typography,
    Grid,
    TextField,
    FormControlLabel,
    Radio,
    RadioGroup
} from '@mui/material'
import React, {FC, useState} from 'react'
import {useDispatch, useSelector} from 'react-redux';
import {PickUpSelector} from "../../../store/selectors/selectors";
import {AppDispatch} from "../../../store/store";
import {decrementStep, setDateData,} from "../../../store/slices/pick-up-slice";
import {AdapterDayjs} from '@mui/x-date-pickers/AdapterDayjs';
import {LocalizationProvider} from '@mui/x-date-pickers/LocalizationProvider';
import {DatePicker} from '@mui/x-date-pickers/DatePicker';
import 'dayjs/locale/ru';
import CustomAlert from "../../../components/alert";
import dayjs, {Dayjs} from "dayjs";
import {TimePicker} from "@mui/x-date-pickers";

const Type: FC = () => {
    const {
        ready: readyStore,
        readyDate: readyDateStore,
        readyTime: readyTimeStore,
        pickUpDate,
        orderNumber,
        powerOfAttorney,
        companyName,
        supplierOrderNumber
    } = useSelector(PickUpSelector)
    const dispatch = useDispatch<AppDispatch>()

    const [open, setOpen] = useState(false)
    const [date, setDate] = useState<Dayjs | null>(pickUpDate)

    const [ready, setReady] = useState(readyStore)

    const [readyDate, setReadyDate] = useState<Dayjs | null>(readyDateStore)
    const [readyTime, setReadyTime] = useState<Dayjs | null>(readyTimeStore)

    const handleBack = () => {
        dispatch(decrementStep())
    }

    const handleSubmit = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);
        const orderNumber = data.get('orderNumber') as string
        const companyName = data.get('companyName') as string
        const supplierOrderNumber = data.get('supplierOrderNumber') as string
        const powerOfAttorney = data.get('powerOfAttorney') as string

        if (
            date && orderNumber && companyName &&
            ((ready === 'заказ не готов' && readyDate && readyTime) || ready === 'заказ готов')
        ) {
            dispatch(setDateData({
                pickUpDate: date,
                orderNumber,
                companyName,
                supplierOrderNumber,
                ready,
                readyDate,
                readyTime,
                powerOfAttorney
            }))
        } else {
            setOpen(true)
        }
    }

    return (
        <>
            <CustomAlert open={open} setOpen={setOpen}/>
            <Typography variant="h6" gutterBottom>
                Укажите следующие данные:
            </Typography>
            <Box component="form" onSubmit={handleSubmit}>
                <Grid container spacing={3}>
                    <Grid item xs={12} sm={6}>
                        <LocalizationProvider
                            dateAdapter={AdapterDayjs}
                            adapterLocale='ru'
                        >
                            <DatePicker
                                minDate={dayjs().add(1, 'day')}
                                label="Дата забора"
                                value={date}
                                onChange={e => setDate(e)}
                                renderInput={(params) => <TextField fullWidth {...params} />}
                            />
                        </LocalizationProvider>
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <TextField
                            required
                            id="orderNumber"
                            name="orderNumber"
                            label="Номер заказа или счета"
                            fullWidth
                            autoComplete="orderNumber"
                            defaultValue={orderNumber}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            id="supplierOrderNumber"
                            name="supplierOrderNumber"
                            label="Номер заказа или счета у поставщика/подрядчика"
                            fullWidth
                            autoComplete="supplierOrderNumber"
                            defaultValue={supplierOrderNumber}
                        />
                    </Grid>

                    <Grid item xs={12}>
                        <RadioGroup
                            aria-labelledby="pick-up-from"
                            name="radio-buttons-group"
                            value={ready}
                            onChange={e => setReady(e.target.value)}
                        >
                            <FormControlLabel name='ready' value="заказ готов" control={<Radio/>} label="Заказ готов"/>
                            <FormControlLabel name='ready' value="заказ не готов" control={<Radio/>}
                                              label="Заказ не готов"/>
                        </RadioGroup>
                    </Grid>

                    {
                        ready === "заказ не готов" &&
                        <Grid item xs={12}>
                            <Grid container spacing={3}>
                                <Grid item xs={12} sm={6}>
                                    <LocalizationProvider
                                        dateAdapter={AdapterDayjs}
                                        adapterLocale='ru'
                                    >
                                        <DatePicker
                                            minDate={dayjs()}
                                            label="Дата готовности"
                                            value={readyDate}
                                            onChange={e => setReadyDate(e)}
                                            renderInput={(params) => <TextField fullWidth {...params} />}
                                        />
                                    </LocalizationProvider>
                                </Grid>
                                <Grid item xs={12} sm={6}>
                                    <LocalizationProvider
                                        dateAdapter={AdapterDayjs}
                                        adapterLocale='ru'
                                    >
                                        <TimePicker
                                            label="Время готовности"
                                            value={readyTime}
                                            onChange={(newValue) => setReadyTime(newValue)}
                                            renderInput={(params) => <TextField fullWidth {...params} />}
                                        />
                                    </LocalizationProvider>
                                </Grid>
                            </Grid>
                        </Grid>
                    }


                    <Grid item xs={12}>
                        <TextField
                            id="powerOfAttorney"
                            name="powerOfAttorney"
                            label="Доверенность"
                            fullWidth
                            autoComplete="powerOfAttorney"
                            defaultValue={powerOfAttorney}
                        />
                    </Grid>
                    <Grid item xs={12}>
                        <TextField
                            required
                            id="companyName"
                            name="companyName"
                            label="Название организации"
                            fullWidth
                            autoComplete="companyName"
                            defaultValue={companyName}
                        />
                    </Grid>
                </Grid>
                <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                    <Button onClick={handleBack}>
                        Назад
                    </Button>
                    <Button variant="contained" type="submit">
                        Далее
                    </Button>
                </Box>
            </Box>
        </>
    )
}

export default Type
