import React, {FC, useEffect, useState} from 'react'
import {Box, Button, CircularProgress, Container, CssBaseline, Grid, Paper, Typography} from "@mui/material";
import Header from "../../../components/header";
import {useSelector} from "react-redux";
import {DeliverySelector} from "../../../store/selectors/selectors";
import {useNavigate} from "react-router-dom";
import Copyright from "../../../components/copyright";
import dayjs from "dayjs";
import axios from "axios";
import CustomAlert from "../../../components/alert";

const DeliveryCheck: FC = () => {
    const [loading, setLoading] = useState(false)
    const [isNext, setIsNext] = useState(false)
    const data = useSelector(DeliverySelector)
    const {
        step,
        to,
        type,
        ready,
        date,
        appNumber,
        fromLastName,
        fromFirstName,
        fromPhone,
        fromExtraPhone,
        fromAddress,
        fromExtraAddress,
        fromCompanyName,
        fromPickUpTimeMin,
        fromPickUpTimeMax,
        fromDocuments,
        nature,
        natureDescription,
        toLastName,
        toFirstName,
        toPhone,
        toExtraPhone,
        toAddress,
        toExtraAddress,
        toCompanyName,
        toPickUpTimeMin,
        toPickUpTimeMax,
        toDocuments,
        needPass,
        call,
        readyDate,
        readyTime,
    }
        = useSelector(DeliverySelector)
    const [success, setSuccess] = useState(false)
    const [error, setError] = useState(false)

    const sendData = () => {
        setLoading(true)
        axios.post('https://закажи-доставку.рф/api-amocrm/delivery', {
            ...data,
            date: dayjs(date).format('DD.MM.YYYY'),
            readyDate: dayjs(readyDate).format('DD.MM.YYYY'),
            readyTime: dayjs(readyTime).format('HH:mm'),
            fromPickUpTimeMin: dayjs(fromPickUpTimeMin).format('HH:mm'),
            fromPickUpTimeMax: dayjs(fromPickUpTimeMax).format('HH:mm'),
            toPickUpTimeMin: dayjs(toPickUpTimeMin).format('HH:mm'),
            toPickUpTimeMax: dayjs(toPickUpTimeMax).format('HH:mm'),
        })
            .then(() => {
                setSuccess(true)
                setIsNext(true)
            })
            .catch(() => setError(true))
            .finally(() => setLoading(false))
    }

    const navigate = useNavigate()

    useEffect(() => {
        if (step < 7) {
            navigate('delivery')
        }
    }, [])

    return (
        <>
            <CustomAlert isSuccess message={'Данные отправлены'} open={success} setOpen={setSuccess}/>
            <CustomAlert message={'Ошибка отправки'} open={error} setOpen={setError}/>
            <CssBaseline/>
            <Header/>
            <Container component="main" maxWidth="md" sx={{mb: 4}}>
                <Paper variant="outlined" sx={{my: {xs: 3, md: 6}, p: {xs: 2, md: 3}}}>
                    <Typography component="h1" variant="h4" align="center">
                        Проверка данных
                    </Typography>
                    <Grid container spacing={3}>
                        <Grid item xs={12} sm={6}>
                            <Typography variant="h6" align="center">
                                От кого везем
                            </Typography>
                            <Typography variant="subtitle1">
                                Тип услуги: {type}
                            </Typography>
                            <Typography variant="subtitle1">
                                Дата забора: {dayjs(date).format('DD.MM.YYYY')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Название организации: {fromCompanyName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Фамилия: {fromLastName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Имя: {fromFirstName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Номер телефона: {fromPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                Доп. номер телефона: {fromExtraPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                Номер заказа: {appNumber}
                            </Typography>
                            <Typography variant="subtitle1">
                                Готовность заказа: {ready}
                            </Typography>
                            {ready === 'заказ не готов' && <Typography variant="subtitle1">
                                Дата готовности заказа: {dayjs(readyTime).format('HH:mm')} {dayjs(readyDate).format('DD.MM.YYYY')}
                            </Typography>}
                            <Typography variant="subtitle1">
                                Время забора с: {dayjs(fromPickUpTimeMin).format('HH:mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Время забора до: {dayjs(fromPickUpTimeMax).format('HH:mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Адрес: {fromAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                Доп. адрес: {fromExtraAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                Укажите тип груза: {nature}
                            </Typography>
                            <Typography variant="subtitle1">
                                Описание груза: {natureDescription}
                            </Typography>
                            <Typography variant="subtitle1">
                                Комментарий: {fromDocuments}
                            </Typography>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <Typography variant="h6" align="center">
                                Кому везем
                            </Typography>
                            <Typography variant="subtitle1">
                                Кому везем: {to}
                            </Typography>
                            <Typography variant="subtitle1">
                                Название организации: {toCompanyName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Фамилия: {toLastName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Имя: {toFirstName}
                            </Typography>
                            <Typography variant="subtitle1">
                                Номер телефона: {toPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                Доп. номер телефона: {toExtraPhone}
                            </Typography>
                            <Typography variant="subtitle1">
                                Время доставки с: {dayjs(toPickUpTimeMin).format('HH:mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Время доставки до: {dayjs(toPickUpTimeMax).format('HH:mm')}
                            </Typography>
                            <Typography variant="subtitle1">
                                Адрес: {toAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                Доп. адрес: {toExtraAddress}
                            </Typography>
                            <Typography variant="subtitle1">
                                Комментарий: {toDocuments}
                            </Typography>
                            <Typography variant="subtitle1">
                                Нужен ли пропуск: {needPass}
                            </Typography>
                            <Typography variant="subtitle1">
                                Нужно ли созвониться заранее: {call}
                            </Typography>
                        </Grid>
                    </Grid>
                    <Box sx={{display: 'flex', justifyContent: 'space-between', mt: 3}}>
                        <Button onClick={() => navigate('/delivery')}>
                            Назад
                        </Button>
                        {(!loading && !isNext) && <Button variant="contained" onClick={sendData}>
                            Подтвердить
                        </Button>}
                        {loading && <Button variant="outlined">
                            <CircularProgress size={20}/>
                        </Button>}
                        {isNext && <Button variant="contained" onClick={() => navigate('/choose')}>
                            На главную
                        </Button>}
                    </Box>
                </Paper>
                <Copyright/>
            </Container>
        </>
    )
}

export default DeliveryCheck
