import React, {FC, useEffect} from 'react'
import {BrowserRouter, Routes, Route, Navigate} from "react-router-dom";
import SignIn from "./pages/sign-in/sign-in";
import SignUp from "./pages/sign-up/sign-up";
import PickUp from "./pages/pick-up/pick-up";
import PickUpCheck from "./pages/pick-up/check/pick-up-check";
import Choose from "./pages/choose/choose";
import Delivery from "./pages/delivery/delivery";
import DeliveryCheck from "./pages/delivery/check/delivery-check";
import {useDispatch, useSelector} from "react-redux";
import {AuthSelector} from "./store/selectors/selectors";
import {refreshThunk} from "./store/thunks/auth-thunk";
import {setLoading} from "./store/slices/auth-slice";
import {CircularProgress} from "@mui/material";

const AppRouter: FC = () => {
    const dispatch = useDispatch()
    const {isAuth, isLoading} = useSelector(AuthSelector)

    useEffect(() => {
        const jwt = localStorage.getItem('jwt')

        if (jwt) {
            // @ts-ignore
            dispatch(refreshThunk({jwt}))
        } else {
            dispatch(setLoading(false))
        }
    }, [])

    if (isLoading) {
        return (
            <div style={{
                height: '100vh',
                width: '100vw',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
            }}>
                <CircularProgress/>
            </div>
        )
    }

    return (
        <BrowserRouter>
            <Routes>
                {
                    !isAuth ?
                        <>
                            <Route path='/sign-in' element={<SignIn/>}/>
                            <Route path='/sign-up' element={<SignUp/>}/>
                            <Route path='*' element={<Navigate to="/sign-in" replace/>}/>
                        </>
                        :
                        <>
                            <Route path='/choose' element={<Choose/>}/>
                            <Route path='/pick-up' element={<PickUp/>}/>
                            <Route path='/pick-up-check' element={<PickUpCheck/>}/>
                            <Route path='/delivery' element={<Delivery/>}/>
                            <Route path='/delivery-check' element={<DeliveryCheck/>}/>
                            <Route path='*' element={<Navigate to="/choose" replace/>}/>
                        </>
                }
            </Routes>
        </BrowserRouter>
    )
}

export default AppRouter
